import cookie from '@/cookie'

class User {
  constructor (token, email, name, role, verified, _id) {
    this.token = token
    this.email = email
    this.name = name
    this.role = role
    this.id = _id
    this.verified = verified
  }
}

export default {
  state: {
    user: null,
    schedule: []
  },
  mutations: {
    authUser (state, payload) {
      state.user = payload
    },
    setSchedule (state, payload) {
      state.schedule = payload
    }
  },
  actions: {
    isUserAuth ({ commit }) {
      if (cookie.getCookie('user') !== undefined) {
        const { token, email, name, role, verified, _id } = cookie.getCookie('user')
        commit('authUser', new User(token, email, name, role, verified, _id))
      }
    },
    async authUser ({ commit }, payload) {
      // делаем запрос к бд с login/password
      commit('clearError')
      commit('setLoading', true)
      try {
        const request = await fetch('http://80.78.248.203:3004/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({ email: payload.login, password: payload.password })
        })

        const response = await request.json()
        commit('setLoading', false)
        return new Promise((resolve, reject) => {
          if (request.status === 200) {
            const { email, name, role, verified, _id } = response.result.user
            const value = JSON.stringify(new User(response.result.token, email, name, role, verified, _id))
            commit('authUser', new User(response.result.token, email, name, role, verified, _id))
            cookie.setCookie('user', value)
            resolve('true')
          } else {
            reject(response.errors)
          }
        })
      } catch (e) {
        console.log(e)
      }
    },
    async getSchedule ({ commit }) {
      const request = await fetch('/data/schedule.json', {
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json'
        }
      })
      const result = await request.json()
      result.forEach((element, index) => {
        result[index].date = element.date.split('T')[0].split('-').reverse().join('-')
      })
      commit('setSchedule', result.filter(a => a.room !== '+'))
    }
  },
  getters: {
    user: state => state.user,
    sh: state => state.schedule
  }
}
