import Vue from 'vue'
import VueRouter from 'vue-router'
import cookie from '@/cookie'
import guard from './guard'
import Home from '@/views/Home'
import NotFound from '@/views/NotFound'
import Feed from '@/views/Feed'
import lk from '@/views/lk'
import Teachers from '@/views/Teachers'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/feed',
    name: 'Feed',
    component: Feed
  },
  {
    path: '/auth',
    name: 'Authorization',
    component: () => import('@/views/Auth')
  },
  {
    path: '/teachers',
    name: 'Teachers',
    component: Teachers
  },
  {
    path: '/lk',
    name: 'lk',
    component: lk,
    redirect: '/lk/schedule',
    children: [
      {
        path: 'schedule',
        name: 'schedule',
        component: () => cookie.getCookie('user').role === 'user' ? import('@/components/student/Schedule') : import('@/components/student/Schedule')
      },
      {
        path: 'progress',
        name: 'progress',
        component: () => cookie.getCookie('user').role === 'user' ? import('@/components/student/Progress') : import('@/components/student/Progress')
      }
    ],
    beforeEnter: guard
  },
  {
    path: '/404',
    name: '404',
    component: NotFound
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
