export default {
  state: {
    feed: []
  },
  mutations: {
    setFeed (state, payload) {
      state.feed = payload
    }
  },
  actions: {
    async loadFeed ({ commit }) {
      commit('clearError')
      commit('setLoading', true)
      await fetch('./data/feed.json')
        .then(response => response.json().then(r => {
          commit('setFeed', r)
          commit('setLoading', false)
        }))
        .catch(e => {
          commit('setLoading', false)
          commit('setError', e)
        })
    }
  },
  getters: {
    feed: state => state.feed
  }
}
