<template>
  <v-card>
    <v-card-title>
      <v-list-item>
        <v-list-item-avatar color="grey darken-3">
          <v-img
            class="elevation-6"
            src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
          ></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>СКФ МТУСИ</v-list-item-title>
          <v-list-item-subtitle class="font-weight-light">{{date}}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-card-title>
    <v-card-text>
      <div class="title">{{title}}</div>
      <v-container fluid v-if="images.length > 0">
        <v-row no-gutters>
          <v-col :cols="getCols(i+1)" v-for="(e, i) in images" :key="e">
            <v-card flat tile class="d-flex ma-1">
              <v-img
              max-height="280"
              contain
              :src="e"
              aspect-ratio
              @click="showFullImage(i)"
              >
              <template v-slot:placeholder>
                <v-row
                  class="fill-height ma-0"
                  align="center"
                  justify="center"
                >
                  <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                </v-row>
              </template>
              </v-img>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <div class="text--primary" :style="{height: !showFullText ? '40px' : 'auto', overflow: 'hidden'}">
        {{text}}
      </div>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions>
      <v-btn
        text
        color="blue darken-2"
        @click="showFullText = !showFullText"
        v-if="!showFullText"
        v-show="text.length > 1"
      >
        Подробнее
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn text>
        <v-icon>mdi-eye-settings-outline</v-icon>
        1231
      </v-btn>
    </v-card-actions>
    <template>
      <v-dialog
        v-model="dialog"
        width="600"
      >
      <v-carousel
      hide-delimiters
      height="auto"
      v-model="fullImage"
      cycle
      >
        <v-carousel-item
        v-for="(e, i) in images"
        :key="i"
        :src="e"
        ></v-carousel-item>
      </v-carousel>
      </v-dialog>
    </template>
  </v-card>
</template>

<script>
export default {
  props: {
    date: String,
    title: String,
    text: String,
    images: Array
  },
  data: () => ({
    breakpoint: '',
    dialog: false,
    fullImage: 0,
    showFullText: false
  }),
  methods: {
    setError (message) {
      this.$store.dispatch('setError', message)
    },
    showFullImage (index) {
      this.fullImage = index
      this.dialog = true
    },
    getCols (i) {
      if (['sm', 'xs'].indexOf(this.$vuetify.breakpoint.name) === -1) {
        return i % 4 === 0 || i === 1 ? 12 : 4
      } else {
        return i % 3 === 0 || i === 1 ? 12 : 6
      }
    }
  }
}
</script>
