<template>
  <v-container fluid>
    <v-row>
      <v-toolbar
      elevation="1"
      dense
      >
        <v-toolbar-title class="title">{{user.name}}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon>
            <v-badge
            color="red"
            overlap
            dot
            >
            <v-icon>mdi-bell</v-icon>
            </v-badge>
          </v-btn>
        <v-btn icon>
          <v-badge
          color="blue darken-1"
          content="+1"
          overlap
          >
            <v-icon>mdi-email</v-icon>
          </v-badge>
        </v-btn>
      </v-toolbar>
    </v-row>
    <v-row>
      <v-col cols="12" md="4" v-if="isMobile">
        <v-row class="flex-column">
          <v-col cols="12" v-for="link in links" :key="link.name">
            <v-card
            :color="link.color+' darken-1'"
            dark
            :disabled="!link.active"
            >
              <v-card-title class="headline">{{link.name}}</v-card-title>
              <v-card-subtitle>{{link.description}}</v-card-subtitle>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn :color="link.color+' darken-3'" tile :to="link.path">Посмотреть</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12" v-else>
        <v-tabs fixed-tabs show-arrows center-active>
          <v-tab v-for="link in links" :key="link.name" :to="link.path" :disabled="!link.active">
            {{link.name}}
          </v-tab>
        </v-tabs>
      </v-col>
      <v-col cols="12" md="8">
        <router-view></router-view>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'lk',
  data: () => ({
    links: [
      { name: 'Расписание занятий', description: 'Расписание занятий для вашей группы', path: 'schedule', color: 'teal', active: true },
      { name: 'Успеваемость', description: 'Модульно-рейтинговая система', path: 'progress', color: 'indigo', active: true },
      { name: 'Портфолио', description: '', path: '/404', color: 'deep-purple', active: false }
    ]
  }),
  computed: {
    user () {
      return this.$store.getters.user
    },
    isMobile () {
      return ['xs', 'sm'].indexOf(this.$vuetify.breakpoint.name) === -1
    }
  }
}
</script>
