<template>
  <v-row justify="center">
    <v-col cols="12" md="9" xl="7" class="mb-2" v-for="e in feed" :key="e.id">
      <feed-component
      :date="e.date"
      :text="e.text"
      :title="e.title"
      :images="e.img"
      />
    </v-col>
  </v-row>
</template>

<script>
import FeedComponent from '@/components/FeedComponent'

export default {
  name: 'Feed',
  components: {
    'feed-component': FeedComponent
  },
  beforeMount () {
    this.$store.dispatch('loadFeed')
  },
  computed: {
    feed () {
      return this.$store.getters.feed
    }
  }
}
</script>
