<template>
<v-app>
  <v-app-bar
    app
    dense
    flat
    clipped-left>
    <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-badge
      color="light-blue darken-3"
      content="version 0.1"
      overlap
      >
      <v-toolbar-title class="px-0" @click="$router.push('/')" style="cursor: pointer;">СКФ МТУСИ</v-toolbar-title>
      </v-badge>
    <v-spacer></v-spacer>
    <v-btn icon @click="switchVuetifyTheme">
      <v-icon>{{getVuetifyTypeTheme ? 'mdi-weather-sunny' : 'mdi-weather-night'}}</v-icon>
    </v-btn>
    <v-btn icon @click="setError('Данная функция появится в ближайшем времени!')">
      <v-icon>mdi-cog-outline</v-icon>
    </v-btn>
    <v-menu
    v-if="user === null && isMobile"
    left
    bottom
    >
    <template v-slot:activator="{ on }">
      <v-btn icon v-on="on">
        <v-icon>mdi-account</v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item link to="/auth">
        <v-list-item-title>Авторизация</v-list-item-title>
      </v-list-item>
    </v-list>
    </v-menu>
    <v-btn v-else-if="isMobile" icon to="/lk">
      <v-icon>mdi-account</v-icon>
    </v-btn>
  </v-app-bar>

  <v-navigation-drawer
  app
  clipped
  width="280"
  v-model="drawer"
  >
    <v-divider></v-divider>
    <v-list>
      <v-list-item link>
        <v-list-item-content>
          <v-list-item-title class="title">Приемная комиссия</v-list-item-title>
          <v-list-item-subtitle>priеm@skf-mtusi.ru</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-divider></v-divider>
    <v-list nav dense shaped>
      <v-list-item link v-for="(e, i) in siteRoutes" :key="i" :to="e.path">
        <v-list-item-icon>
          <v-icon>mdi-{{e.icon}}</v-icon>
        </v-list-item-icon>
        <v-list-item-title>{{e.title}}</v-list-item-title>
      </v-list-item>
    </v-list>
    <v-divider></v-divider>
    <v-list nav>
      <v-list-item link v-for="(e, i) in menuLinks" :key="i" :to="e.path">
        <v-list-item-icon>
          <v-icon>mdi-{{e.icon}}</v-icon>
        </v-list-item-icon>
        <v-list-item-title>{{e.title}}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>

  <!-- Sizes your content based upon application components -->
  <v-content>

    <!-- Provides the application the proper gutter -->
    <v-container fluid class="pt-0">
      <!-- If using vue-router -->
      <router-view></router-view>
    </v-container>
  </v-content>

  <v-bottom-navigation
    class="d-md-none"
    app
    color="teal"
    grow
  >
    <v-btn v-for="e in siteRoutes" :key="e.title" :to="e.path">
      <span>{{e.title}}</span>
      <v-icon>mdi-{{e.icon}}</v-icon>
    </v-btn>
    <v-btn to="/lk">
      <span>Аккаунт</span>
      <v-icon>mdi-account</v-icon>
    </v-btn>
  </v-bottom-navigation>
  <!-- Вывод системных сообщений -->
  <v-snackbar
  v-model="error.hasError"
  color="indigo darken-2"
  vertical
  top
  :timeout="6000"
  >
  {{error.errorMessage}}
  <v-btn
    text
    @click="closeError"
  >
    Закрыть
  </v-btn>
  </v-snackbar>
</v-app>
</template>

<script>
// import Weather from './components/Weather'

export default {
  name: 'App',
  data: () => ({
    drawer: false,
    siteRoutes: [
      { title: 'Главная', path: '/', icon: 'compass-outline' },
      { title: 'Новости', path: '/feed', icon: 'newspaper-variant-outline' }
    ],
    menuLinks: [
      { title: 'Научно-педагогический состав', path: '/teachers', icon: 'teach' }
    ]
  }),
  methods: {
    switchVuetifyTheme () {
      this.$vuetify.theme.dark = !this.getVuetifyTypeTheme
    },
    closeError () {
      this.$store.dispatch('clearError')
    },
    setError (message) {
      this.$store.dispatch('setError', message)
    }
  },
  computed: {
    getVuetifyTypeTheme () {
      return this.$vuetify.theme.dark
    },
    isMobile () {
      return ['xs', 'sm'].indexOf(this.$vuetify.breakpoint.name) === -1
    },
    error () {
      return this.$store.getters.error
    },
    user () {
      return this.$store.getters.user
    }
  },
  beforeMount () {
    this.drawer = ['xs', 'sm'].indexOf(this.$vuetify.breakpoint.name) === -1
    this.$store.dispatch('isUserAuth').catch()
  }
}
</script>

<style lang="scss">
  .v-list-item__title {
    white-space: normal !important;
  }
</style>
