<template>
  <v-row>
    <v-col cols="12" md="6" xl="4" v-for="e in teacher" :key="e.id">
      <Teacher
      :id="e.id"
      :name="e.name"
      :photo="e.photo"
      :specification="e.specification"
      :power="e.power"
      @showFullInfo="$event => showFullInfo = $event"
      />
    </v-col>
    <v-col cols="4">
      <v-dialog v-model="showFullInfo" max-width="600">
        <v-card>
          <v-card-title class="title">Еще не придумал как это будет выглядеть</v-card-title>
        </v-card>
      </v-dialog>
    </v-col>
  </v-row>
</template>

<script>
import Teacher from '@/components/Teacher'

export default {
  name: 'Teachers',
  data: () => ({
    showFullInfo: false
  }),
  computed: {
    teacher () {
      return this.$store.getters.teacher
    }
  },
  beforeMount () {
    this.$store.dispatch('loadTeacher')
  },
  components: {
    Teacher
  }
}
</script>
